<template>
  <div class="terminal" ref="terminal">
    <span class="sep">*======================== Asad Awadia ===========================================*</span>
    <p>Welcome to my personal page</p>
    <p>Type <span class="yellow help" @click.prevent="type('help')">help</span> to get a list of commands you can enter </p>
    <span class="sep">*======================= Asad Awadia ============================================*</span>
    <div v-for="item in responses" class="terminal-line">
        <prompt v-if="item.type === 'command'" :active="false" :text="item.text" :directory="item.directory"></prompt>
        <response v-else :content="item.content" :type="item.type"></response>
    </div>
    <prompt :active="true" :visible="promptActive" v-on:emitCommand="saveCommand"></prompt>
  </div>
</template>

<script>
  import Prompt from './Prompt'
  import response from './responses/createResponse'

  export default {
    name: 'terminal',
    components: {
      Prompt,
      response
    },
    data () {
      return {
        promptActive: true,
        responses: [],
        commands: []
      }
    },
    created () {
      this.registerCommands()
    },
    methods: {
      parseCommand (string) {
        var commands = string.split(' && ')
        for (var i = 0; i < commands.length; i++) {
          var argv = commands[i].split(/ +/)
          var name = argv[0]
          // take the name out of the args list
          argv.shift()

          this.runCommand(name, argv)
        }
      },
      activatePrompt () {
        this.promptActive = true
        this.$nextTick(() => {
          this.scrollBottom()
        })
      },
      deactivatePrompt () {
        this.promptActive = false
      },
      registerCommands () {
        this.command(['help', 'h'], () => {
          this.createResponse('help', this.commands)
        }, ['Show the help function... which you have to know to view this.'])

        this.command(['about'], (argv) => {
          this.createResponse('message', 'I am primarily a backend developer helping you build distributed, reactive, microservices on the cloud using Kotlin, Vert.x, and Kubernetes')
        }, [`Learn more about me`])

        this.command(['education'], (argv) => {
          this.createResponse('message', 'I graduated from the University of Waterloo in April 2018 with a Bachelor of Computing and Financial Management')
          this.createResponse('message', 'My undergrad was a combination of Computer Science and Accounting and financial management')
          this.createResponse('message', 'I am currently enrolled in a Masters of Computer Science program at Wilfrid Laurier University with Fall 2019 being my first semester')
        }, ['Get my education credentials'])

        this.command(['resume'], (argv) => {
          this.createResponse('message', 'Loading resume...')
          window.open('static/resume.pdf')
        }, ['Get my full resume'])

        this.command(['contact'], (argv) => {
          this.createResponse('message', 'Phone Number: (519) 498-0534 ')
          this.createResponse('message', 'Email: Asad.Awadia@yahoo.com')
        }, ['Get my contact information'])

        this.command(['github'], (argv) => {
          this.createResponse('message', 'Taking you to GitHub...')
          window.open('https://github.com/asad-awadia')
        }, ['Go to my github'])

        this.command(['linkedin'], (argv) => {
          this.createResponse('message', 'Taking you to LinkedIn...')
          window.open('https://www.linkedin.com/in/asadawadia/')
        }, ['Go to my LinkedIn'])

        this.command(['hi'], () => {
          this.createResponse('message', 'Hey hey')
        }, ['Say hello to the computer'])

        this.command(['echo'], (argv) => {
          this.createResponse('message', argv.join(' '))
        }, ['Echo out a string onto the command line', 'echo <hello world!>'])

        this.command(['clear'], () => {
          this.responses = []
          this.activatePrompt()
        }, ['Clear the terminal of any responses'])
      },
      command (name, func, help) {
        this.commands.push({
          name: name,
          command: func,
          help: help
        })
      },
      runCommand (name, argv) {
        for (var i = 0; i < this.commands.length; i++) {
          for (var j = 0; j < this.commands[i].name.length; j++) {
            if (this.commands[i].name[j] === name) {
              return this.commands[i].command(argv)
            }
          }
        }

        this.createResponse('message', 'That command is not recognized: ' + name)
      },
      saveCommand (string) {
        this.deactivatePrompt()

        let command = {
          type: 'command',
          text: string
        }

        this.responses.push(command)

        setTimeout(() => {
          this.parseCommand(string)
        }, 20)
      },

      createResponse (type, content) {
        let response = {
          type: type,
          content: content
        }

        this.responses.push(response)
        this.activatePrompt()
      },

      scrollBottom () {
        this.$refs.terminal.scrollTop = this.$refs.terminal.scrollHeight
      }
    }
  }
</script>

<style>

input {
  font-size: 100%;
  background: inherit;
  border: none;
}

.terminal-line {
  min-height: 22px;
}

@media screen and (min-width: 1800px) {
  .terminal-line {
    max-width: 70%;
  }
}

.terminal-line > * {
  display: inline-block;
  vertical-align: middle;
  text-align: start;
}

.terminal {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow-y: auto;
  padding: 15px;
  margin-right: auto;
}

.sep {
  color: #b48ead;
}

.yellow {
  color: #ebcb8b;
}

</style>



