<template>
  <div class="loading" v-if="!loaded">
    <pre class="ascii-logo">
    _                  _      _                    _ _
   / \   ___  __ _  __| |    / \__      ____ _  __| (_) __ _
  / _ \ / __|/ _` |/ _` |   / _ \ \ /\ / / _` |/ _` | |/ _` |
 / ___ \\__ \ (_| | (_| |  / ___ \ V  V / (_| | (_| | | (_| |
/_/   \_\___/\__,_|\__,_| /_/   \_\_/\_/ \__,_|\__,_|_|\__,_|
    </pre>
    <progressBar :percentage="percentage" :length="120"></progressBar>
  </div>
</template>

<script>
import progressBar from './progressBar'

export default {
  name: 'loading',
  props: ['loading'],
  data () {
    return {
      percentage: 0,
      loaded: false
    }
  },
  created () {
    setInterval(() => {
      if (this.percentage < 100) {
        this.percentage++
      } else if (!this.loaded) {
        this.loaded = true
        this.$emit('loaded')
      }
    }, 20)
  },
  components: {
    progressBar
  }
}
</script>

<style>

  .ascii-logo {
    color: #d08770;
    display: table;
    margin: 1em auto;
    font-size: 2em;
  }

</style>



